body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --desktop-heading-h6: "Sofia Pro";
  --desktop-body-bold-medium: Graphik;
  --font-sora: Sora;

  /* font sizes */
  --desktop-heading-h6-size: 1.25rem;
  --button-medium-size: 1rem;
  --desktop-body-bold-large-size: 1.13rem;
  --desktop-display-bold-medium-size: 1.56rem;
  --desktop-body-bold-small-size: 0.88rem;
  --desktop-display-bold-large-size: 2.44rem;

  /* Colors */
  --base-grey-100: #1e1f25;
  --base-darker-grey: #141518;
  --base-grey-0: #fff;
  --base-grey-80: #34384c;
  --base-dark-transparent: rgba(47, 50, 65, 0.5);
  --base-grey-60: #5d6588;
  --base-grey-40: #a5adcf;
  --accent-red-primary: #fa2256;
  --accent-green-primary: #11cabe;
  --color-darkslateblue: #252d55;

  /* Gaps */
  --gap-5xs: 0.5rem;
  --gap-5xl: 1.5rem;
  --gap-17xl: 2.25rem;
  --gap-base: 1rem;
  --gap-9xs: 0.25rem;
  --gap-11xs: 0.13rem;

  /* Paddings */
  --padding-5xs: 0.5rem;
  --padding-base: 1rem;
  --padding-xl: 1.25rem;
  --padding-13xl: 2rem;

  /* border radiuses */
  --br-xs: 12px;
  --br-11xs: 2px;
  --br-31xl: 50px;
  --br-5xs: 8px;
  --br-81xl: 100px;
  --br-10xs: 3px;
}
